.spinner-wrapper {
  width: 100%;
  height: 100vh;
  background: #161616;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner-wrapper img {
  display: block;
  width: 250px;
  height: auto;
  -webkit-animation-timing-function: ease-in;
          animation-timing-function: ease-in;
  -webkit-animation-name: rotating;
          animation-name: rotating;
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
@-webkit-keyframes rotating {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  100% {
    top: -50px;
    left: -67px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}
@keyframes rotating {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }
  100% {
    top: -50px;
    left: -67px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
  }
}

@media only screen and (min-width: 992px) {
  .nav {
    width: 65px;
    height: 100vh;
    padding-bottom: 20px;
    background: #161616;
    position: fixed;
    font-size: 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }

  .nav a {
    text-decoration: none;
    color: gray;
  }

  .nav ul {
    list-style-type: none;
    padding: 0;
  }

  .nav li {
    margin: 20px 0px;
    text-align: center;
    transition: 0.5s;
    cursor: pointer;
  }

  .nav li a {
    position: relative;
    transition: 0.5s;
    text-align: center;
  }

  .nav li a::after {
    content: "";
    font-size: 10px;
    position: absolute;
    line-height: 25px;
    top: 0px;
    left: -21px;
    opacity: 0;
    width: 65px;
    height: 30px;
    background: #161616;
    text-align: center;
    transition: 0.5s;
  }

  .nav li a:hover::after {
    opacity: 1;
    transition: 0.5s;
    color: #0089c9;
    font-size: 11px;
    font-weight: bold;
  }
  .nav li a:active,
  .nav li a.active {
    color: #0089c9;
  }

  .nav li .first-nav-item::after {
    content: "HOME";
  }

  .nav li .second-nav-item::after {
    content: "ABOUT";
    width: 60px;
  }

  .nav li .third-nav-item::after {
    content: "SKILLS";
  }

  .nav li .fourth-nav-item::after {
    content: "PROJECTS";
  }

  .nav li .fifth-nav-item::after {
    content: "CONTACT";
  }

  .nav .linked-in-icon {
    transition: 0.5s;
  }

  .nav .linked-in-icon:hover {
    color: #0089c9;
    transition: 0.5s;
  }
}
